'use server'

import { createClient } from '@/utils/supabase/server'
// ‚úÖ ÂºïÂÖ•‰ª£ÁêÜÂ∫ì
import { setGlobalDispatcher, ProxyAgent } from 'undici';

// ============================================================
// üî• Âº∫Âà∂‰ª£ÁêÜË°•‰∏Å
// ============================================================
if (process.env.NODE_ENV === 'development') {
  try {
    const proxyUrl = 'http://127.0.0.1:7890'; // ‰Ω†ÁöÑÁ´ØÂè£
    const dispatcher = new ProxyAgent({
      uri: proxyUrl,
      connect: {
        timeout: 60000 // Â¢ûÂä†ËøûÊé•Ë∂ÖÊó∂Âà∞ 60Áßí
      }
    });
    setGlobalDispatcher(dispatcher);
  } catch (err) {
    console.error('‰ª£ÁêÜËÆæÁΩÆÂ§±Ë¥•:', err);
  }
}

// Â∏ÆÂä©ÂáΩÊï∞ÔºöÂª∂ËøüÁ≠âÂæÖ
const delay = (ms: number) => new Promise(resolve => setTimeout(resolve, ms));

export async function generateShotImage(shotId: string, prompt: string, projectId: string) {
  const supabase = await createClient();
  
  // üîÑ ÈáçËØïÊú∫Âà∂ÔºöÊúÄÂ§öÂ∞ùËØï 3 Ê¨°
  const MAX_RETRIES = 3;
  
  for (let i = 0; i < MAX_RETRIES; i++) {
    try {
      // 1. ÊûÑÈÄ†ËØ∑Ê±Ç
      // üí° ÊèêÈÄüÂ∞èÊäÄÂ∑ßÔºöÂ¶ÇÊûú‰Ω†ËßâÂæóÂ§™ÊÖ¢ÔºåÂèØ‰ª•Êää‰∏ãÈù¢ÁöÑ model=flux ÊîπÊàê model=turbo
      const encodedPrompt = encodeURIComponent(prompt + ", cinematic lighting, 8k, photorealistic, monochrome sketch style");
      const imageUrl = `https://image.pollinations.ai/prompt/${encodedPrompt}?width=1280&height=720&model=turbo&seed=${Math.random()}`; 

      // 2. ‰∏ãËΩΩÂõæÁâá (ËÆæÁΩÆ 60Áßí Ë∂ÖÊó∂)
      const response = await fetch(imageUrl, {
        signal: AbortSignal.timeout(60000) // 60ÁßíÂêéÊâçÁÆóË∂ÖÊó∂
      });
      
      if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
      }

      const arrayBuffer = await response.arrayBuffer();
      const imageBuffer = Buffer.from(arrayBuffer);

      // 3. ‰∏ä‰º†Âà∞ Supabase
      const fileName = `${projectId}/${shotId}_${Date.now()}.png`;

      const { error: uploadError } = await supabase
        .storage
        .from('shots')
        .upload(fileName, imageBuffer, {
          contentType: 'image/png',
          upsert: true
        });

      if (uploadError) throw new Error("‰∏ä‰º†Â≠òÂÇ®Ê°∂Â§±Ë¥•");

      // 4. Ëé∑ÂèñÈìæÊé•
      const { data: { publicUrl } } = supabase
        .storage
        .from('shots')
        .getPublicUrl(fileName);

      // 5. Êõ¥Êñ∞Êï∞ÊçÆÂ∫ì (Â¶ÇÊûú‰∏çÊòØ‰∏¥Êó∂È¢ÑËßà)
      if (shotId && !shotId.startsWith('storyboard_')) {
          await supabase.from('shots').update({
              image_url: publicUrl,
              status: 'completed'
          }).eq('id', shotId);
      }

      // ‚úÖ ÊàêÂäü‰∫ÜÔºÅÁõ¥Êé•ËøîÂõûÔºåË∑≥Âá∫Âæ™ÁéØ
      return { success: true, url: publicUrl };

    } catch (error: any) {
      console.warn(`‚ö†Ô∏è Á¨¨ ${i + 1} Ê¨°ÁîüÊàêÂ§±Ë¥•:`, error.message);
      
      // Â¶ÇÊûúÊòØÊúÄÂêé‰∏ÄÊ¨°Â∞ùËØïËøòÂú®Â§±Ë¥•ÔºåÈÇ£Â∞±ÁúüÁöÑÊäõÂá∫ÈîôËØØ
      if (i === MAX_RETRIES - 1) {
        return { success: false, message: `Â§öÊ¨°ÈáçËØïÂêéÂ§±Ë¥•: ${error.message}` };
      }
      
      // Á≠âÂæÖ 1 ÁßíÂêéÂÜçÈáçËØï
      await delay(1000);
    }
  }
  
  return { success: false, message: "Êú™Áü•ÈîôËØØ" };
}